.page {
  font-family: $font-text;
  font-size: 18px;
  line-height: 30px;
  color: $color-black;
  background-color: $color-white;
}

.page__body {
  margin: 0;
  padding-top: 40px;
}

.page__heading {
  font-family: $font-heading;
  font-size: 50px;
  line-height: 50px;
  font-weight: 800;
}

.page__subheading {
  font-family: $font-heading;
  font-size: 18px;
  line-height: normal;
  font-weight: 800;
  letter-spacing: 3.6px;
}
